  ,mounted:function(){
    axios.post('/im/search-srv/im_group_views/page',{"filters":{"AND":[{"OR":[]},{"group.memberCount":{"NE":0}},{"tenantId":{"EQ":"dc90ef20143f437e8144b827c19ec6ea"}}]},"pageNo":1,"pageSize":20,"sort":[{"ignoreCase":false,"property":"ctime","type":"DSC"}]}
)
      .then(function (res) {
          console.log(res);
      })
      .catch(function (error) {

      });
