<template>
  <div class="index_full pd5">
    <div class="tableHead">
      <Button type="primary" @click="createRelation">创建关系</Button> 
    </div>
    <div class="tableWrap" ref="tableWrap">
      <Table :columns="columns" :data="data" :height="height" border></Table>
    </div>
    <div class="tablePage">
      
    </div>
  </div>
</template>

<script>
import choose from './components/choose.vue'
const axios = require('axios');
import handle from '../../assets/js/options'
//Vue.component('choose',choose);
export default {
  components:{
    choose
  }
  ,data () {
    return {
      columns: [
          {
              title: 'Name',
              key: 'name'
          },
          {
              title: 'Age',
              key: 'age'
          },
          {
              title: 'Address',
              key: 'address'
          }
      ],
      data: [
          {
              name: 'John Brown',
              age: 18,
              address: 'New York No. 1 Lake Park',
              date: '2016-10-03'
          },
          {
              name: 'Jim Green',
              age: 24,
              address: 'London No. 1 Lake Park',
              date: '2016-10-01'
          },
          {
              name: 'Joe Black',
              age: 30,
              address: 'Sydney No. 1 Lake Park',
              date: '2016-10-02'
          },
          {
              name: 'Jon Snow',
              age: 26,
              address: 'Ottawa No. 2 Lake Park',
              date: '2016-10-04'
          }
      ]
      ,height:0
      ,allocationModal:false
    }
  }
  ,methods:{
    createRelation:function(){
      //this.allocationModal = true;
      var that = this;
      var options = {
          render: () => {
            var h = that.$createElement;
              return h('choose')
          }
          ,width:"200px;"
          ,onOk:function(comp){
            console.log(comp.getData());
          }
      };
      options = handle(options);
      this.$Modal.confirm(options);
    }
  }
  ,mounted:function(){
    this.$nextTick(function () {
      this.height = this.$refs.tableWrap.clientHeight;
    })
  }
}
</script>

<style scoped>
 @import './css/index.css';
</style>



